}
footer{
    background-color: white;
    position: fixed;
    bottom: 0px;
    z-index: 1;
    width: 100%;
    height: 50px;
    box-shadow: 0 30px 70px 5px gray;
    border-top: 1px solid rgb(224, 224, 224);
