.profile-body{
    margin-top: 50px;
    width: 900px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

}
.profile-header{
    width: 75%;
    margin-top: 15px;
    display: flex;
    flex-direction: row;
    padding-left:50px;
    padding-right:50px;
    justify-content: space-between;
    align-items: center;

}
.profile-info{
    width: 350px;
}
.profile-picture{
    height: 250px;
    width: 250px;
    border: solid 1px rgba(128, 128, 128, 0.199);
    border-radius: 100%;
    background-image: url(/spacestagram/static/media/default.ad04c91f.jpg);
    background-position: center;
    background-size: cover;
}
.profile-posts{
    width: 100%;
    height: auto;
    display: grid;
    grid-template-columns: 300px 300px 300px;
    grid-template-rows: repeat(auto, 300px);    
}
.profile-posts-title{
    margin-top: 50px;
    margin-bottom: 10px;
    padding-bottom: 8px;
    width: 100%;
    border-bottom: 1px solid rgba(128, 128, 128, 0.397);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.profile-post-item{
    width: 290px;
    height: 290px;
    margin-bottom: 10px;
    background-position: center;
    background-size: cover;
    transition: 0.1s;
}

@media(max-width:901px){
